* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.countres_flag {
  display: block;
  width: 30px;
  height: auto;
}

.country-list {
  padding: 0;
  list-style: none;
}

.country_text {
  font-size: 30px;
  padding: 0;
  margin: 0;
}

.country_title {
  font-weight: 700;
}


.flagAndName {
  display: flex;
  align-items: center;
  padding: 0;
  gap: 10px;
}
